import uniPopup from '@/components/uni-popup/uni-popup.vue'
import xselect from '../../wxcomponents/xselect/xselect.vue'
export default {
	onLoad() {
		uni.request({
			url: 'https://120.24.180.246:8080/xmRepair/phoneBrand/getBrand',
			method: 'GET',
			data: {},
			success: res => {
				this.sildeList = res.data.data
				console.log(res.data.data)
			},
			fail: (err) => {
				console.log(err)
			}
		})
		uni.request({
			url: 'https://120.24.180.246:8080/xmRepair/phoneBrand/getPhoneType?id=1',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data.data)
			},
			fail: () => {},
			complete: () => {}
		})
	},
	data() {
		return {
			colorId:null,
			selectedId: 0,
			popupContent: [{
				imgSrc: '../../wxcomponents/popupcon/gold@2x.png',
				text: '金色'
			}, {
				imgSrc: '../../wxcomponents/popupcon/rose@2x.png',
				text: '玫瑰金'
			}, {
				imgSrc: '../../wxcomponents/popupcon/silver@2x.png',
				text: '银色'
			}, {
				imgSrc: '../../wxcomponents/popupcon/black@2x.png',
				text: '黑色'
			}],
			isSelected: false,
			phone: '手机',
			pad: '平板',
			sildeList: [],
			phoneType: [{
				type: 'iPhone XS Max'
			}, {
				type: 'iPhone XS'
			}, {
				type: 'iPhone XR'
			}, {
				type: 'iPhone X'
			}]
		}
	},
	methods: {
		onNext() {
			uni.navigateTo({
				url: '../faults/faults'
			});
		},
		openPopup() {
			this.$refs.popup.open()
		},
		closePopup() {
			this.$refs.popup.close()
		},
		onSelectedId(e) {
			this.selectedId = e.currentTarget.id
			console.log(e.currentTarget.id)
		},
		onSelectedColor(e){
			this.colorId = e.currentTarget.id
			// console.log(e.currentTarget)
		}

	},
	components: {
		uniPopup,
		xselect
	}
}
